<template>
  <div class="user-menu">
    <md-speed-dial
      md-open="click"
      md-direction="top"
      class="md-fab-bottom-right button"
      style="bottom: 8px; right: 8px;"
    >
      <!-- <md-button class="md-fab" md-fab-trigger>
        <md-icon md-icon-morph>close</md-icon>
        <md-icon>settings</md-icon>
      </md-button> -->

      <md-button
        class="md-icon-button"
        md-fab-trigger
        style="padding: 0;"
      >
        <md-avatar style="pointer-events: none;">
          <img src="https://placebear.com/100/100" alt="Avatar">
        </md-avatar>
      </md-button>

      <md-button
        class="md-fab md-primary md-mini md-clean"
        v-for="button in buttons"
      >
        <md-icon>{{ button.icon }}</md-icon>
      </md-button>
    </md-speed-dial>
  </div>
</template>

<script>
export default {
  name: 'UserMenu',
  computed: {
    buttons () {
      return [
        { icon: 'email' },
        { icon: 'info' },
        { icon: 'star' },
        { icon: 'phone' }
      ]
    }
  }
}
</script>

<style scoped>
  .user-menu {
    z-index: 200;
  }
</style>



